import React, { Component } from 'react';
import Link from 'next/link';
import Router, { withRouter } from 'next/router';
import NProgress from 'nprogress';
import Nav from './Nav';
import UserDropdown from './DropdownMenu';
import { HeaderStyles } from './styles/HeaderStyles';
import Logo from '../elements/Logo';
import Icon from '../elements/Icon';
import User from './User';

Router.onRouteChangeStart = () => {
  NProgress.start();
};

Router.onRouteChangeComplete = () => {
  NProgress.done();
};

Router.onRouteChangeError = () => {
  NProgress.done();
};

class Header extends Component {
  state = {
    dropdownMenuVisible: false,
  };
  render() {
    const { router } = this.props;

    if (router.pathname !== '/sign-in') {
      return (
        <HeaderStyles>
          <div className="header-inner">
            <Link href="/">
              <a>
                <Logo />
              </a>
            </Link>
            <Nav dept={router.query.dept} pathname={router.pathname} />
            <User>
              {({ data: { me } }) => {
                if (me) {
                  return (
                    <>
                      <button
                        styles={{ border: 'none', background: 'transparent' }}
                      >
                        <Icon
                          name="dropdownMenu"
                          height="35px"
                          onClick={() => {
                            this.setState({
                              dropdownMenuVisible: !dropdownMenuVisible,
                            });
                          }}
                        />
                      </button>
                      {this.state.dropdownMenuVisible && <UserDropdown />}
                    </>
                  );
                }

                return (
                  <Link href="/sign-in">
                    <a>Sign In</a>
                  </Link>
                );
              }}
            </User>
          </div>
        </HeaderStyles>
      );
    }

    return <></>;
  }
}

export default withRouter(Header);
